export default {
	data() {
		return {
			
		}
	},
	methods: {
		goLiyi:function(){
			uni.navigateTo({
				url:'../liyijiansuo/liyijiansuo'
			})
		},
		goTongji:function(){
			uni.navigateTo({
				url:'../tongji/tongji'
			})
		},
		goWenDang:function(){
			uni.navigateTo({ 
				url:'../wendang/wendang'
			})
		},
		tuichu:function(){
			var _self =this
			uni.request({
				url:_self.$api+"Login/Exit_Landing",
				data:{sessionId:uni.getStorageSync('sessionId')},
				success:function(res){
					console.log(res)
					 if(res.data == true){
						  uni.removeStorageSync('userId')
						  uni.removeStorageSync('sessionId')
						  uni.removeStorageSync('Rule_Id')
						  uni.removeStorageSync('Expiration_Date') 
						  uni.removeStorageSync('Username')
						  uni.navigateTo({
						  	url: '../login/login',
						  });
					}
					},
					fail:function(){
						   uni.removeStorageSync('userId')
						   uni.removeStorageSync('sessionId')
						   uni.removeStorageSync('Rule_Id')
						   uni.removeStorageSync('Expiration_Date') 
						   uni.removeStorageSync('Username')
						    uni.navigateTo({
						   	url: '../login/login',
						   });
					}
					
				
			})  
			},
	},
	
}
